.author {
	font-weight: 700;
	line-height: 54px;
	text-align: right;
}

.epigraph {
	font-style: italic;
	line-height: 36px;
	margin: 30px auto 0;
	text-align: right;
	width: 320px;
}


@media (min-width: 768px) {
	.epigraph {
		width: 404px;
	}
}

@media (min-width: 1281px) {
	.epigraph {
		width: 954px;
	}

	q {
		display: block;
		margin: 0 0 0 550px;
		width: 404px;
	}
}
